import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    username: '',
    tel: '',
    region: [, , ,]
  },
  getters: {
    // {{ this.$store.getters.方法名 }}
    getLength(state) {
      return state.username.length;
    }
  },
  // 只能修改state的值，不能异步ajax请求
  mutations: {
    login(state, payLoad) {
      state.username = payLoad.username;
      state.tel = payLoad.tel;
    },
    logout(state) {
      state.username = '';
      state.tel = '';
    },
    saveRegion(state, payLoad) {
      state.region = payLoad;
    }

  },
  // 1、可以异步ajax请求 2、提交的是mutations,不能直接变更状态
  actions: {
  },
  modules: {
  }
})
