<template></template>
<script>
import { bus } from "../main";
export default {
  name: "user-header",
  data() {
    return {
      isLogined: Boolean,
      username: "",
    };
  },
  methods: {
    getLoginInfo() {
      this.isLogined =
        localStorage.getItem("islogined") == "true" ? true : false;
      this.username = localStorage.getItem("username");
    },
  },
  mounted() {
    this.getLoginInfo();
    // if (!this.isLogined) {
    //   this.$messagebox.alert("请先登录！");
    //   this.$router.push("/login");
    // }
    bus.$emit("getLoginInfo", {
      username: this.username,
      isLogined: this.isLogined,
    });
  },
};
</script>